	module.exports = {"data":{"markdownRemark":{"html":"<h2>Sorting Algorithms</h2>\n<p>Sorting algorithms are a set of instructions that take an array or list as an input and arrange the items into a particular order.</p>\n<p>Sorts are most commonly in numerical or a form of alphabetical (called lexicographical) order, and can be in ascending (A-Z, 0-9) or descending (Z-A, 9-0) order.</p>\n<h3>Why Sorting Algorithms are Important</h3>\n<p>Since sorting can often reduce the complexity of a problem, it is an important algorithm in Computer Science. These algorithms have direct applications in searching algorithms, database algorithms, divide and conquer methods, data structure algorithms, and many more.</p>\n<h3>Some Common Sorting Algorithms</h3>\n<p>Some of the most common sorting algorithms are:</p>\n<ul>\n<li>Selection Sort</li>\n<li>Bubble Sort</li>\n<li>Insertion Sort</li>\n<li>Merge Sort</li>\n<li>Quick Sort</li>\n<li>Heap Sort</li>\n<li>Counting Sort</li>\n<li>Radix Sort</li>\n<li>Bucket Sort</li>\n</ul>\n<h3>Classification of Sorting Algorithm</h3>\n<p>Sorting algorithms can be categorized based on the following parameters:</p>\n<ol>\n<li>\n<p>Based on Number of Swaps or Inversion\nThis is the number of times the algorithm swaps elements to sort the input. <code>Selection Sort</code> requires the minimum number of swaps.</p>\n</li>\n<li>\n<p>Based on Number of Comparisons\nThis is the number of times the algorithm compares elements to sort the input. Using <a href='https://guide.freecodecamp.org/computer-science/notation/big-o-notation/' target='_blank' rel='nofollow'>Big-O notation</a>, the sorting algorithm examples listed above require at least <code>O(nlogn)</code> comparisons in the best case and <code>O(n^2)</code> comparisons in the worst case for most of the outputs.</p>\n</li>\n<li>\n<p>Based on Recursion or Non-Recursion\nSome sorting algorithms, such as <code>Quick Sort</code>, use recursive techniques to sort the input. Other sorting algorithms, such as <code>Selection Sort</code> or <code>Insertion Sort</code>, use non-recursive techniques. Finally, some sorting algorithm, such as <code>Merge Sort</code>, make use of both recursive as well as non-recursive techniques to sort the input.</p>\n</li>\n<li>\n<p>Based on Stability\nSorting algorithms are said to be <code>stable</code> if the algorithm maintains the relative order of elements with equal keys. In other words, two equivalent elements remain in the same order in the sorted output as they were in the input.</p>\n</li>\n<li>\n<p><code>Insertion sort</code>, <code>Merge Sort</code>, and <code>Bubble Sort</code> are stable</p>\n</li>\n<li>\n<p><code>Heap Sort</code> and <code>Quick Sort</code> are not stable</p>\n</li>\n</ol>","fields":{"slug":"/algorithms/sorting-algorithms/"},"frontmatter":{"title":"Sorting Algorithms"}}},"pathContext":{"slug":"/algorithms/sorting-algorithms/"}}

